.button{
	color: $color-secondary;
	background-color: $color-third;
	padding: 10px 25px;
	border: 0;
	position: relative;
	display: inline-block;
	opacity: 0.85;
	box-shadow: 2px 4px 8px rgba(0,0,0,0.1);
	@include transition(all 0.6s ease);
	@include rounded(50px);
	span{
		font-size: 14px;
	}
	&:hover{
		opacity: 1;
	}

	&.button--green{
		background-color: $color-fourth;
	}
	&.button--to-top{
		position: fixed;
		z-index: 6;
		right: 40px;
		bottom: -100px;
		width: 64px;
	    height: 64px;
	    padding: 15px;
	    text-align: center;
	    &.button--to-top--show{
	    	bottom: 40px;
	    	@include bp-768{
	    		bottom: 25px;
	    	}
	    }
	    @include bp-768{
	    	right: 25px;
	    }
	}
}