.materi{
	position: relative;
	z-index: 3;
	top: -110px;
	.divider--trigger{
		top: -130px;
	}
	.materi__wrapper{
		width: 55%;
		margin: auto;
		padding: 50px 65px 60px;
		@include rounded(8px);
		background: #fff;
		box-shadow: 0 4px 25px rgba(0,0,0,0.06);
		text-align: center;
		@include bp-768{
			width: 90%;
			padding: 50px 35px 60px;
		}
		@include bp-425{
			width: 95%;
			padding: 50px 20px 60px;
		}

		h2{
			font-size: 24px;
			color: $color-primary;
			line-height: 38px;
			@include bp-768{
				font-size: 20px;
				line-height: 28px;
			}
		}
		.divider{
			margin-bottom: 20px;
			margin-top: 20px;
		}
		p{
			font-size: 14px;
		}
		.materi__button{
			margin-top: 25px;
			.button{
				margin: 10px 15px;
			}
		}
	}
}