.header{
	background: #708cff; /* Old browsers */
	background: -moz-linear-gradient(left, #708cff 0%, #7db9e8 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(left, #708cff 0%,#7db9e8 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to right, #708cff 0%,#7db9e8 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#708cff', endColorstr='#7db9e8',GradientType=1 );
	color: $color-secondary;
	position: relative;
	padding: 150px 15px 165px;
	z-index: 1;
	.header__wrapper{
		position: relative;
		z-index: 2;
		text-align: center;
		h2{
			font-size: 40px;
			font-weight: normal;
			text-shadow: 2px 4px 8px rgba(0,0,0,0.08);
			line-height: 54px;
			@include bp-768{
				font-size: 30px;
				line-height: 40px;
			}
		}
		ul{
			margin-top: 25px;
			li{
				display: inline-block;
				vertical-align: top;
				padding: 25px;
				h3{
					font-size: 18px;
					margin: 12px 0 3px 0;
				}
				p{
					font-size: 14px;
				}
				@include bp-768{
					width: 25%;
					padding: 0 10px;
					img{
						width: 90px;
					}
				}
				@include bp-425{
					width: 50%;
					padding: 20px 10px;
				}
			}
		}
	}
	.header__bg{
		position: absolute;
		z-index: 1;
		width: 100%;
		bottom: 0;
		margin-left: -15px;
		margin-right: -15px;
		img{
			width: 100%;
			display: block;
		}
	}
}