footer{
	text-align: center;
	position: relative;
    top: -35px;
    padding-bottom: 65px;
    .divider{
    	margin-bottom: 50px;
    }
    .footer__wrapper{
		width: 550px;
		margin: auto;
		position: relative;
		@include bp-768{
			width: 90%;
		}

		.footer__wrapper__logo{
			position: absolute;
			@include bp-768{
				position: relative;
			}
		}
		.footer__wrapper__text{
			padding-left: 155px;
			padding-top: 7px;
			text-align: left;
			@include bp-768{
				padding-left: 0;
				text-align: center;
			}

			h4{
				font-size: 18px;
			}
			p{
				font-size: 13px;
				line-height: 18px;
				margin-top: 7px;
			}
		}
    }
}